import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, PlanoSaude } from '../../../../models/dtos/mfp/planoSaude';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const PlanoSaudeSearchView: FC<SearchViewProps<PlanoSaude>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: false,
      text: 'Código',
    },
    {
      dataField: 'razaoSocial',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Razão Social',
    },
    {
      dataField: 'cnpj',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'CNPJ',
    },
    {
      dataField: 'registroANS',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Registro ANS',
    },
  ];
  return (
    <WindowComponent title="Manutenção de Plano de Saúde" wikiId={2338}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'PlanoSaudeEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default PlanoSaudeSearchView;
